'use client'
import React, { useState } from 'react';
import { Search, Plus, MoreHorizontal, ArrowUpDown, Calendar, Pin, ChevronDown, Maximize2, Filter } from 'lucide-react';

const TodoWidget = () => {
  const [todos, setTodos] = useState([
    {
      id: 1,
      title: 'From Agent: Redesign the Skyline website',
      category: 'P2',
      date: null,
      completed: false,
      pinned: false,
      type: 'email'
    },
    {
      id: 2,
      title: 'problems of software team because of erp',
      category: null,
      date: 'Aug 4',
      completed: false,
      pinned: true,
      type: 'task'
    },
    {
      id: 3,
      title: 'discussion on software about software etc products',
      category: null,
      date: 'Aug 4',
      completed: false,
      pinned: true,
      type: 'task'
    },
    {
      id: 4,
      title: 'discussion on  cold emailing',
      category: null,
      date: 'Aug 4',
      completed: false,
      pinned: true,
      type: 'task'
    },
    {
      id: 5,
      title: 'have a meeting with  tom and alex regarding team',
      category: null,
      date: 'Aug 4',
      completed: false,
      pinned: false,
      type: 'task'
    }
  ]);

  const [newTodo, setNewTodo] = useState('');
  const [searchQuery, setSearchQuery] = useState('');
  const [showAllTasks, setShowAllTasks] = useState(true);

  const addTodo = () => {
    if (newTodo.trim()) {
      const todo = {
        id: Date.now(),
        title: newTodo.trim(),
        category: null,
        date: new Date().toLocaleDateString('en-US', { month: 'short', day: 'numeric' }),
        completed: false,
        pinned: false,
        type: 'task'
      };
      setTodos([...todos, todo]);
      setNewTodo('');
    }
  };

  const toggleTodo = (id) => {
    setTodos(todos.map(todo => 
      todo.id === id ? { ...todo, completed: !todo.completed } : todo
    ));
  };

  const togglePin = (id) => {
    setTodos(todos.map(todo => 
      todo.id === id ? { ...todo, pinned: !todo.pinned } : todo
    ));
  };

  const assignToCalendar = (id) => {
    // This would integrate with Google Calendar API
    const todo = todos.find(t => t.id === id);
    console.log('Assigning to Google Calendar:', todo);
    
    // Simulate calendar integration
    setTodos(todos.map(t => 
      t.id === id ? { ...t, assignedToCalendar: true } : t
    ));
    
    alert(`Task "${todo.title}" has been assigned to Google Calendar!`);
  };

  const filteredTodos = todos.filter(todo => 
    todo.title.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <div className="bg-[#1c1c1c]/50 backdrop-blur-md border border-gray-700/30 rounded-2xl hover:bg-[#1c1c1c]/30 transition-all duration-300 p-4 mb-4">
      {/* Header */}
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-white font-medium">To-dos</h2>
        
        {/* Search Bar */}
        <div className="relative flex-1 mx-2">
          <Search className="w-4 h-4 text-gray-400 absolute left-3 top-1/2 transform -translate-y-1/2" />
          <input
            type="text"
            placeholder="Search to-dos"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full bg-white-800/50 border border-gray-600/50 rounded-md pl-10 pr-4 py-2 text-sm text-white placeholder-gray-400 focus:outline-none focus:border-gray-500"
          />
        </div>
        
        <div className="flex items-center gap-2">
          <button className="p-1 hover:bg-gray-800/30 rounded">
            <Maximize2 className="w-4 h-4 text-gray-400" />
          </button>
          <button className="p-1 hover:bg-gray-800/30 rounded">
            <MoreHorizontal className="w-4 h-4 text-gray-400" />
          </button>
        </div>
      </div>
      
      {/* Controls */}
      <div className="flex items-center justify-between mb-3">
        <button className="flex items-center gap-2 text-white text-sm bg-gray-800/50 px-3 py-1 rounded hover:bg-gray-800/30">
          <Plus className="w-4 h-4" />
          <span>All</span>
          <ChevronDown className="w-4 h-4" />
        </button>
        
        <div className="flex items-center gap-2">
          <button className="p-1 hover:bg-gray-800/30 rounded">
            <ArrowUpDown className="w-4 h-4 text-gray-400" />
          </button>
          <button className="p-1 hover:bg-gray-800/30 rounded">
            <Filter className="w-4 h-4 text-gray-400" />
          </button>
        </div>
      </div>

      {/* User Email */}
      <div className="text-gray-400 text-xs mb-4">skylineagent@trymartin.com</div>

      {/* Todo List - Scrollable with fixed height for ~3 items */}
      <div className="space-y-2 max-h-[180px] overflow-y-auto pr-1 custom-scrollbar">
        {filteredTodos.map((todo) => (
          <div key={todo.id} className="flex items-start gap-3 p-2 hover:bg-gray-800/30 rounded-md cursor-pointer">
            <input
              type="checkbox"
              checked={todo.completed}
              onChange={() => toggleTodo(todo.id)}
              className="mt-1 w-4 h-4 bg-transparent border border-gray-600 rounded"
            />
            
            <div className="flex-1">
              <p className={`text-white text-sm ${todo.completed ? 'line-through text-gray-500' : ''}`}>
                {todo.title}
              </p>
              
              <div className="flex items-center gap-2 mt-1">
                {todo.category && (
                  <span className="text-yellow-500 text-xs bg-yellow-500/20 px-2 py-0.5 rounded">
                    {todo.category}
                  </span>
                )}
                {todo.date && (
                  <span className="text-gray-400 text-xs">{todo.date}</span>
                )}
                {todo.assignedToCalendar && (
                  <span className="text-xs text-green-400">📅 In Calendar</span>
                )}
              </div>
            </div>
            
            <div className="flex items-center gap-1">
              <button
                onClick={() => togglePin(todo.id)}
                className={`p-1 rounded hover:bg-gray-800/30 ${todo.pinned ? 'text-yellow-400' : 'text-gray-500'}`}
                title={todo.pinned ? 'Unpin' : 'Pin'}
              >
                <Pin className="w-3 h-3" />
              </button>
              
              <button
                onClick={() => assignToCalendar(todo.id)}
                className="p-1 rounded hover:bg-gray-800/30 text-blue-400"
                title="Assign to Google Calendar"
              >
                <Calendar className="w-3 h-3" />
              </button>
              
              <Plus className="w-4 h-4 text-gray-400 hover:text-white cursor-pointer" />
            </div>
          </div>
        ))}
      </div>

      {/* Add New Todo */}
      <div className="flex items-center space-x-2 mt-4">
        <input
          type="text"
          placeholder="Add a new task..."
          value={newTodo}
          onChange={(e) => setNewTodo(e.target.value)}
          onKeyPress={(e) => e.key === 'Enter' && addTodo()}
          className="flex-1 px-3 py-2 bg-gray-800/50 border border-gray-600/50 rounded-md text-sm focus:outline-none focus:border-gray-500 placeholder-gray-400 text-white"
        />
        <button
          onClick={addTodo}
          className="p-2 bg-blue-600 hover:bg-blue-700 rounded-md transition-colors"
        >
          <Plus className="w-4 h-4 text-white" />
        </button>
      </div>
      
      

      <style jsx>{`
        .custom-scrollbar::-webkit-scrollbar {
          width: 4px;
        }
        .custom-scrollbar::-webkit-scrollbar-track {
          background: rgba(55, 65, 81, 0.3);
          border-radius: 2px;
        }
        .custom-scrollbar::-webkit-scrollbar-thumb {
          background: rgba(156, 163, 175, 0.5);
          border-radius: 2px;
        }
        .custom-scrollbar::-webkit-scrollbar-thumb:hover {
          background: rgba(156, 163, 175, 0.7);
        }
      `}</style>
    </div>
  );
};

export default TodoWidget;